* {
	box-sizing: border-box; }

html {
  height: 100%;
  font-size: 100%;
  line-height: 1.3;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0;
  font-family: $familyFont;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white; }

article,
aside,
footer,
header,
nav,
section {
  display: block; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

p {
  margin: 0; }

a {
  text-decoration: none;
  -webkit-text-decoration-skip: objects;
  background-color: transparent; }

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none; }

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: $familyFont;
  font-size: 100%;
  line-height: inherit; }

input[type="button"],
input[type="submit"],
button {
  background-color: white;
  cursor: pointer; }

b,
strong {
  font-weight: inherit; }

figcaption,
figure,
main {
  display: block; }

figure {
  margin: 0; }

hr {
  box-sizing: content-box;
  overflow: visible;
  height: 0; }

pre {
  font-family: monospace, monospace;
  font-size: 1rem; }

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1rem; }

dfn {
  font-style: italic; }

mark {
  background-color: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25rem; }

sup {
  top: -0.5rem; }

audio,
video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: rem-calc(1) dotted ButtonText; }

fieldset {
  padding: 0; }

legend {
  display: table;
  padding: 0;
  max-width: 100%;
  color: inherit;
  white-space: normal; }

progress {
  display: inline-block;
  vertical-align: baseline; }

textarea {
  overflow: auto;
  resize: none; }

[type="checkbox"],
[type="radio"] {
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline: none; }

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

details,
menu {
  display: block; }

summary {
  display: list-item; }

canvas {
  display: inline-block; }

template {
  display: none; }

[hidden] {
  display: none; }
